.navigation {
  width: 100%;
  height: 4em;
  display: flex;
  align-items: center;
}

.toolbar {
  display: flex;
  width: 100%;
  padding: 0 1em;
  align-items: center;
}

.menu-bar {
  color: #d8d8d8;
  font-size: 1.5em;
  &:hover {
    cursor: pointer;
  }
}

.logo {
  margin-left: 1em;
  flex-grow: 1;
  font-size: 1.5em;
}

.icons {
  display: flex;
  justify-content: space-between;
  gap: 1em;
  margin-right: 1em;
}

.icon {
  color: #d8d8d8;
  padding: 0;
  margin: 0;
  font-size: 1.5em;
}

.code {
  color: #d3d3d3;
  font-weight: 900;
}

.name {
  font-weight: 900;
  color: #87CEEB;
}