@import "sidebar";
@import "navigation";

html, body {
  margin: 0;
  background: linear-gradient(to bottom, #262335, #2B3D5D);
  font-family: Open Sans;
}

a {
  color: #87CEEB
}

.main {
  width: 80%;
  margin: auto;
  display: flex;
  gap: 2em;
}

.main-content {
  width: 70%;
  height: calc(100vh - 4rem);
  overflow-y: auto;
  scroll-behavior: smooth;
  &::-webkit-scrollbar {
    display: none;
  }
}


.container {
  height: 100%;
}

.section {
  min-height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 2.5em;

  & h2 {
    color: #87CEEB;
    font-size: 2em;
    font-weight: 800;
    margin: 0;
  }

  & h3 {
    color: #b6b6b6;
    margin: 0;
  }

  & p {
    color: #d8d8d8;
    margin: 0.5em 0;
    font-size: 1.1em;
    font-weight: 500;
    & b {
      font-weight: 900;
      font-size: 1.1em;
    }
  }

  & ul {
    list-style-type: none;
  }

  & h3 {
    font-size: 1.5em;
    font-weight: 800;
  }
}

.greeting {
  text-indent: 10em;
}

.project {
  margin-bottom: 1.5em;

  &:after { 
    content: "."; 
    visibility: hidden; 
    display: block; 
    height: 0;
  }
}

.project-pic {
  float: right;
  margin: 0 0 0.25em 0.25em;
  border-radius: 0.4em;
}

.contact-tools {
  width: 100%;
  padding: 1em 0;
  overflow-x: auto;
  white-space: nowrap;
  & .tool {
    padding: 0 1em;
    display: inline-block;
    border: 0.1em solid #d8d8d8;
    border-radius: 0.5em;
    min-width: 15em;
    height: 17em;

    &:hover {
      cursor: pointer;
    }

    
    & a {
      text-decoration: none;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: #d8d8d8;
      & i {
        font-size: 5em;
      }
    }
  }
  &::-webkit-scrollbar {
    height: 1em;
    border-radius: 1em;
    background-color: #d8d8d8;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 1em;
    background-color: #87CEEB;
  }
}