.sidebar {
  width: 30%;
  height: calc(100vh - 4em);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile-pic {
  border-radius: 50%;
  margin-bottom: 2em;
}

ul {
  list-style-type: none;
}

.anchor-item {
  margin-bottom: 1em;
}

.anchor {
  display: flex;
  font-size: 1.5em;
  font-weight: bolder;
  text-decoration: none;
  & div {
    margin-right: 0.5em;
  }
}

.active-anchor {
  color: #d8d8d8;
  & div {
    animation: move 1s infinite;
  }
}


@keyframes move {
  0% { transform: translateX(0px); }
  50% {transform: translateX(5px); }
  100% { transform: translateX(0px); }
}